






import Vue from 'vue'
import {Inject, Model, Prop, Watch, Emit} from 'vue-property-decorator'
import Semantic, { VueSemantic } from 'lib/module'

@Semantic('sidebar',
{
	direction: {type: String, required: true}
}, {
	//context
	exclusive: Boolean,
	closable: Boolean,
	dimPage: Boolean,
	scrollLock: Boolean,
	returnScroll: Boolean,
	delaySetup: Boolean,
	transition: String
}, [
	'show',
	'hide'
])
export default class Sidebar extends VueSemantic {
	@Model('change') visible: boolean
	@Watch('visible') setVisible(v: boolean) {
		this.semantic(v?'show': 'hide');
	}
	created() {
		if(this.visible) {	//If initialised with true `visible`
			//this.$el;//add 'visible(? pushable)'
		}
	}
	configure(config) {
		config.context = this.$el.parentElement;
		config.onVisible = ()=> {
			this.$emit('change', true);
			this.$emit('visible');
		}
		config.onHidden = ()=> {
			this.$emit('change', false);
			this.$emit('hidden');
		}
	}
}
